import { Injectable } from '@angular/core';
import { UtilService } from '../common/util.service';

@Injectable()
export class FridgeService {

  constructor(private us: UtilService) { }

  fridgeServiceSubscription = function (obj, conf, powerSubject, doorSubject) {

    let debug = false;

    var bulbDevice;
    var doorDevice;
    var shelfDevice;

    var storedBulb = this.us.readStoredDevice("bulbDevice");
    var storedDoor = this.us.readStoredDevice("doorDevice");

    if(!storedBulb){
      bulbDevice = conf.smartBulbUuid;
    }
    
    if(!storedDoor){
      doorDevice = conf.doorUuid;
    }

    var config = {
      fridgeDevices: {
        bulb: true,
        meter: true,
        door: true,
        shelf: true
      },
      token: conf.token,
      url: conf.proxyUrl,
      doorUuid: doorDevice,
      bulbUuid: bulbDevice
    };
    
    // Importing SmartFridge instance
    var smartFridge = new obj.window.SmartFridge(config);

    smartFridge.on('error', err => {
      console.error(">>> SmarShelf Service Error:", err);
      //process.exit(-1);
    });

    smartFridge.on('shelfData', data => {
      console.log("SHELF DATA");
        var newShelf = this.us.newShelf(data);
        this.us.updateShelves(obj, newShelf);
    });

    smartFridge.on('scaleData', data => {
      console.log(
        "SCALE",
        "id:", data.id,
        (debug ? "" : "raw: " + data.hexData),
        "weight:", data.weightLbs
      );
    });

    smartFridge.on('doorData', data => {
      console.log(
        "DOOR",
        "id:", data.id,
        (debug ? "" : "raw: " + data.hexData),
        "doorOpened:", data.doorOpened
      );
      doorSubject.next(data.doorOpened);
    });

    smartFridge.on('powerData', data => {
        powerSubject.next(data.powerConsumptionMilliWatts/1000);
      console.log(
        "POWER",
        "id:", data.id,
        "powerMilliWatts:", data.powerConsumptionMilliWatts
      );
    });

    smartFridge.on('bulbData', data => {
      console.log(
        "BULB",
        "id:", data.id,
        "color:", data.color
      );
    });



  };
}



